import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Avatar from "@material-ui/core/Avatar";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    verticalAlign: "middle",
    width: "100%",
  },
  avatar: {
    height: "85px",
    width: "85px",
    backgroundColor: theme.palette.primary.main,
    fontSize: "40px",
  },
}));

const ContactProfile = (props) => {
  const classes = useStyles();
  const { contactFirstName, contactLastName, email } = props.selectedContact;
  if (!props.selectedContact) return null;
  return (
    <div className={classes.root}>
      {contactFirstName && contactLastName ? (
        <Avatar
          className={classes.avatar}
        >{`${contactFirstName[0]}${contactLastName[0]}`}</Avatar>
      ) : null}

      <Typography variant="h6">{`${contactFirstName} ${contactLastName}`}</Typography>
      <Typography variant="subtitle1">{email}</Typography>
    </div>
  );
};
export default ContactProfile;
