import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import { useQuery, useInfiniteQuery } from "react-query";
import { makeStyles } from "@material-ui/core/styles";
import { fetchData, sendData } from "../services/httpUtil";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import MessageContent from "../components/messageContent";
import ShowCommentReply from "../components/showCommentReply";
import InputText from "../components/inputText";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CircularProgress from "@material-ui/core/CircularProgress";
import { RefresherComponent } from "../components/helper";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from "@material-ui/core/TextField";
import { Waypoint } from "react-waypoint";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import { ResizableBox } from "react-resizable";
import "../components/style/styles.css";
// import "react-resizable/css/styles.css";

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100vh",
    display: "flex",
    flexFlow: "column",
  },
  grow: {
    flexGrow: 1,
    overflow: "auto",
  },
  input: {
    width: "100%",
    overflow: "auto",
  },
  messagesSection: {
    flex: "1",
    overflow: "auto",
    paddingBottom: "5px",
  },
  main: {
    flex: "1",
    display: "flex",
    flexFlow: "column",
    overflow: "auto",
  },
  cardRoot: {
    backgroundColor: "rgba(255, 255, 255, 0)",
    boxShadow: "0px 0px 0px 0px",
    height: "64px",
  },
}));

const PAGE_SIZE = 5;

export default function MessageInfo({
  threadRefetch,
  selectedThread,
  onChangeStatus,
  isChangingThreadStatus,
}) {
  const classes = useStyles();
  const [isOnlyOne, setIsOnlyOne] = useState(false);
  const [assigneeMenuAnchorEl, setAssigneeMenuAnchorEl] = useState(null);
  const [statusMenuAnchorEl, setStatusMenuAnchorEl] = useState(null);
  const [userAssignee, setUserAssignee] = useState("");
  const [listOfFilteredUsers, setListOfFilteredUsers] = useState([]);
  const [changeAssigneeStatus, setChangeAssigneeStatus] = useState("");
  const [totalMessagesCount, setTotalMessagesCount] = useState(0);
  const [isSendingMessage, setIsSendingMessage] = useState(false);
  async function getMoreMessages(queryKey, page = 0) {
    let data = await fetchData(
      `threads/${selectedThread?.id}/messages?page=${page}&pageSize=${PAGE_SIZE}`
    );
    setTotalMessagesCount(data.totalCount);
    return { data: data.data, page };
  }

  const {
    data: messages,
    status: messagesStatus,
    refetch: messagesRefresh,
    fetchMore: fetchMoreMessages,
    canFetchMore: canFetchMoreMessages,
    isFetchingMore: isFetchingMoreMessages,
  } = useInfiniteQuery({
    queryKey: `threads/${selectedThread?.id}/messages`,
    queryFn: getMoreMessages,
    config: {
      getFetchMore: (lastGroup, allGroups) =>
        lastGroup.page * PAGE_SIZE >= totalMessagesCount
          ? false
          : lastGroup.page + 1,
      refetchOnWindowFocus: false,
      enabled: selectedThread ? true : false,
    },
  });

  const { data: threadsStatuses, status: threadsStatusesStatus } = useQuery({
    queryKey: `threads/status`,
    queryFn: fetchData,
    config: {
      refetchOnWindowFocus: false,
      staleTime: 50000,
    },
  });

  const [selectedAssignee, setSelectedAssignee] = useState();
  useEffect(() => {
    setSelectedAssignee(
      selectedThread &&
        selectedThread.assignee &&
        selectedThread.assignee.firstName
        ? `${selectedThread.assignee.firstName} ${selectedThread.assignee.lastName}`
        : "Unassigned"
    );
  }, [selectedThread]);

  const handleAssigneeClick = (event) => {
    setAssigneeMenuAnchorEl(event.currentTarget);
  };

  const handleAssigneeClose = () => {
    setAssigneeMenuAnchorEl(null);
  };

  const { data: users, status: usersStatus } = useQuery({
    queryKey: `users`,
    queryFn: fetchData,
    config: {
      refetchOnWindowFocus: false,
    },
  });

  async function changeAssignee(userId, username) {
    setChangeAssigneeStatus("loading");
    try {
      await sendData(
        `reassign?assigneeId=${userId}&threadId=${selectedThread.id}`
      );
      setChangeAssigneeStatus("success");
      //---in case of success set the assignee
      setSelectedAssignee(username);
      threadRefetch();
    } catch (error) {
      setChangeAssigneeStatus("error");
      console.log(error);
      return null;
    }
  }

  useEffect(() => {
    let filteredUsers;
    if (userAssignee.length > 2 && users && users.length > 0) {
      let upperCaseUserAssignee = userAssignee.toUpperCase();
      filteredUsers = users.filter((user) => {
        if (user.firstName.toUpperCase().indexOf(upperCaseUserAssignee) > -1) {
          return true;
        }
        if (user.lastName.toUpperCase().indexOf(upperCaseUserAssignee) > -1) {
          return true;
        }
        if (user.email.toUpperCase().indexOf(upperCaseUserAssignee) > -1) {
          return true;
        }
        return false;
      });
      setListOfFilteredUsers(filteredUsers);
    } else {
      setListOfFilteredUsers([]);
    }
  }, [userAssignee, users]);

  useEffect(() => {
    setIsOnlyOne(false);
    if (messages && messages[0].data.length === 1) {
      setIsOnlyOne(true);
    }
  }, [messages]);

  return (
    <div className={classes.root}>
      <AppBar position="sticky">
        <Toolbar>
          {messages && messages[0].data.length > 0 ? (
            <>
              {usersStatus === "loading" ||
              changeAssigneeStatus === "loading" ? (
                <CircularProgress color="secondary" />
              ) : (
                <Card className={classes.cardRoot}>
                  <CardHeader
                    style={{ cursor: "pointer" }}
                    avatar={<AccountBoxIcon />}
                    title={Boolean(assigneeMenuAnchorEl) ? null : "Assignee"}
                    subheader={
                      Boolean(assigneeMenuAnchorEl) ? null : selectedAssignee
                    }
                    onClick={handleAssigneeClick}
                  />
                </Card>
              )}

              <div className={classes.grow} />
              {threadsStatusesStatus === "success" &&
              threadsStatuses.length > 0 ? (
                <Button
                  aria-controls="status-menu-button"
                  aria-haspopup="true"
                  onClick={(e) => setStatusMenuAnchorEl(e.currentTarget)}
                  endIcon={<ArrowDropDownIcon />}
                >
                  {selectedThread.status}
                </Button>
              ) : (
                <CircularProgress color="secondary" />
              )}

              <Menu
                id="status-menu"
                anchorEl={statusMenuAnchorEl}
                keepMounted={false}
                open={Boolean(statusMenuAnchorEl)}
                onClose={() => setStatusMenuAnchorEl(null)}
              >
                {threadsStatuses.map((ts) => {
                  if (ts === selectedThread.status) return null;
                  return (
                    <MenuItem
                      key={ts}
                      onClick={() => {
                        onChangeStatus(ts);
                        setStatusMenuAnchorEl(null);
                      }}
                    >
                      {`Mark as ${ts}`}
                    </MenuItem>
                  );
                })}
              </Menu>
              {/* ---- */}

              <StyledMenu
                id="simple-menu"
                anchorEl={assigneeMenuAnchorEl}
                keepMounted={false}
                open={Boolean(assigneeMenuAnchorEl)}
                onClose={handleAssigneeClose}
                style={{
                  marginLeft: "45px",
                  marginTop: "-10px",
                }}
              >
                <div style={{ padding: "2px" }}>
                  <TextField
                    placeholder="User"
                    value={userAssignee}
                    onChange={(e) => setUserAssignee(e.target.value)}
                    fullWidth
                  />
                </div>
                {usersStatus === "loading" ? (
                  <CircularProgress />
                ) : listOfFilteredUsers && listOfFilteredUsers.length > 0 ? (
                  listOfFilteredUsers.map((user) => {
                    return (
                      <div
                        key={user.id}
                        style={{
                          backgroundColor: "white",
                          border: "1px solid #ffc107",
                        }}
                      >
                        <MenuItem
                          onClick={(e) => {
                            //TODO: set Assignee to selected user
                            changeAssignee(
                              user.id,
                              `${user.firstName} ${user.lastName}`
                            );
                            handleAssigneeClose();
                          }}
                        >
                          <Card style={{ boxShadow: "none", width: "100%" }}>
                            <CardHeader
                              avatar={<AccountBoxIcon />}
                              title={`${user.firstName} ${user.lastName}`}
                              subheader={user.email}
                            />
                          </Card>
                        </MenuItem>
                      </div>
                    );
                  })
                ) : null}
              </StyledMenu>
            </>
          ) : null}
        </Toolbar>
      </AppBar>

      {isChangingThreadStatus ? (
        <div style={{ margin: "auto" }}>
          <CircularProgress />
        </div>
      ) : messages && messages.length > 0 ? (
        <div className={classes.main}>
          <div className={classes.messagesSection}>
            {messages.map((msgGroup, i) => {
              return (
                <React.Fragment key={i}>
                  {msgGroup.data &&
                    msgGroup.data.map((msg) => {
                      switch (msg.type) {
                        case "EMAIL":
                          return (
                            <MessageContent
                              key={msg.id}
                              message={msg}
                              isOnlyOne={isOnlyOne}
                            />
                          );
                        case "COMMENT":
                        default:
                          return (
                            <ShowCommentReply
                              key={msg.id}
                              message={msg}
                              isOnlyOne={isOnlyOne}
                            />
                          );
                      }
                    })}
                </React.Fragment>
              );
            })}
            {!isFetchingMoreMessages &&
            canFetchMoreMessages * PAGE_SIZE < totalMessagesCount ? (
              <Waypoint onEnter={() => fetchMoreMessages()} />
            ) : isFetchingMoreMessages ? (
              <div
                style={{
                  margin: "10px 0px",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <CircularProgress />
              </div>
            ) : (
              <h4
                style={{
                  textAlign: "center",
                  color: "grey",
                }}
              >
                end of the list
              </h4>
            )}
          </div>
          <div className={classes.input}>
            <InputText
              threadStatus={selectedThread.status}
              threadId={selectedThread.id}
              messagesRefresh={messagesRefresh}
              onIsSendingMessage={(state) => setIsSendingMessage(state)}
            />
          </div>
        </div>
      ) : messagesStatus === "loading" || isSendingMessage ? (
        <div style={{ margin: "auto" }}>
          <CircularProgress />
        </div>
      ) : messagesStatus === "error" ? (
        <RefresherComponent handleRefresh={messagesRefresh} />
      ) : (
        <h2 style={{ textAlign: "center" }}>Select a thread to see messages</h2>
      )}
    </div>
  );
}

const StyledMenu = withStyles({
  paper: {
    backgroundColor: "#ffffff00",
  },
})((props) => <Menu elevation={0} getContentAnchorEl={null} {...props} />);
