import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    flex: "1",
    overflow: "auto",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0,
  },
  scroll: {
    display: "flex",
    flexDirection: "column",
    // width: "inherit",
    maxWidth: "90%",
  },
}));

export default function ContactDetails(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState("panel1");
  const { selectedContact } = props;

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root}>
      <Accordion
        expanded={expanded === "panel1"}
        onChange={handleChange("panel1")}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
          style={{ backgroundColor: "#d4d4d4" }}
        >
          <Typography className={classes.heading}>
            About this contact
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <div>
            <Typography variant="subtitle1">First Name:</Typography>
            <Typography variant="subtitle2">
              {selectedContact && selectedContact.contactFirstName
                ? selectedContact.contactFirstName
                : "Unknown"}
            </Typography>
            <br />
            <Typography variant="subtitle1">Last Name:</Typography>
            <Typography variant="subtitle2">
              {selectedContact && selectedContact.contactLastName
                ? selectedContact.contactLastName
                : "Unknown"}
            </Typography>
            <br />
            <Typography variant="subtitle1">Email:</Typography>
            <Typography variant="subtitle2">
              {selectedContact && selectedContact.email
                ? selectedContact.email
                : "No Email Address"}
            </Typography>
            <br />
            <Typography variant="subtitle1">Phone number:</Typography>
            <Typography variant="subtitle2">
              {selectedContact && selectedContact.telephone
                ? selectedContact.telephone
                : "No Phone Number"}
            </Typography>
            <br />
            <Typography variant="subtitle1">Company name:</Typography>
            <Typography variant="subtitle2">
              {selectedContact && selectedContact.companyName
                ? selectedContact.companyName
                : "No Company Name"}
            </Typography>
          </div>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
