export async function fetchData(url) {
  let token = localStorage.getItem("TOKEN");
  try {
    let header = new Headers();
    header.append("x-auth-token", token);

    let requestOptions = {
      method: "GET",
      headers: header,
      redirect: "follow",
    };
    let result = await fetch(
      process.env.REACT_APP_API_ENDPOINT + url,
      requestOptions
    );
    result = await result.json();
    if (result.totalCount) {
      result = { data: result.results, totalCount: result.totalCount };
    } else {
      result = result.results;
    }
    return result;
  } catch (error) {
    console.log(error);
    throw error;
  }
}
export async function sendData(url, body) {
  let token = localStorage.getItem("TOKEN");
  try {
    let header = new Headers();
    header.append("x-auth-token", token);
    header.append("Content-Type", "application/json");

    let requestOptions = {
      method: "POST",
      headers: header,
      redirect: "follow",
      body: JSON.stringify(body),
    };
    let result = await fetch(
      process.env.REACT_APP_API_ENDPOINT + url,

      requestOptions
    );
    return result;
  } catch (error) {
    console.log(error);
    throw error;
  }
}

