import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import SearchBox from "../components/searchBox";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Typography from "@material-ui/core/Typography";
import FirstCharOfName from "../components/firstCharOfName";
import FilterMenuForThreads from "../components/filterMenuForThreads";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import CircularProgress from "@material-ui/core/CircularProgress";
import Paper from "@material-ui/core/Paper";
import Skeleton from "@material-ui/lab/Skeleton";
import Box from "@material-ui/core/Box";
import Zoom from "@material-ui/core/Zoom";
import SearchIcon from "@material-ui/icons/Search";
import IconButton from "@material-ui/core/IconButton";
import { fetchData, sendData } from "../services/httpUtil";
import { useQuery, useInfiniteQuery } from "react-query";
import { RefresherComponent, showDate } from "../components/helper";
import MessageInfo from "./messageInfo";
import Customer from "./customer";
import { Waypoint } from "react-waypoint";
import { ResizableBox } from "react-resizable";
import "../components/style/styles.css";
// import "react-resizable/css/styles.css";

const useStyles = makeStyles((theme) => ({
  root: {
    //overflow: "hidden",
    width: "100%",
    height: "100vh",
    display: "flex",
  },
  list: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
    position: "relative",
    overflow: "auto",
    height: "calc(100vh - 66px)",
  },
  listSection: {
    backgroundColor: "inherit",
  },
  ul: {
    backgroundColor: "inherit",
    padding: 0,
  },
  inline: {
    display: "inline",
  },
  searchInputOpen: {
    width: "80%",
    overflow: "hidden",
    backgroundColor: "#cecece",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  searchInputClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(5) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(5) + 1,
    },
  },
  hide: {
    display: "none",
  },
  tools: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    width: "100%",
    backgroundColor: theme.palette.primary.main,
    boxShadow: "0px 0px 0px 0px",
  },
  toolbar: {
    // display: "flex",
    // justifyContent: "center",
    width: "100%",
  },
  space: {
    flexGrow: 10,
  },
  threads: {
    height: "100vh",
    [theme.breakpoints.down("sm")]: {
      height: "35vh",
      overflow: "auto",
    },
  },
  messageInfo: {
    height: "100vh",
    [theme.breakpoints.down("sm")]: {
      height: "55vh",
      overflow: "auto",
    },
  },
  content: {
    width: "100%",
  },
}));

const Threads = ({ channelID }) => {
  const PAGE_SIZE = 10;
  const classes = useStyles();
  const [filter, setFilter] = useState("");
  const [isChangingThreadStatus, setIsChangingThreadStatus] = useState(false);
  const [showSearchInput, setShowSearchInput] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [searchByQuery, setSearchByQuery] = useState(false);
  const [totalThreadsCount, setTotalThreadsCount] = useState(0);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight);
  useEffect(() => {
    window.addEventListener("resize", () =>
      setWindowHeight(window.innerHeight)
    );
  }, []);

  const { data: threadStatuses, status: threadStatusesStatus } = useQuery({
    queryKey: `threads/status`,
    queryFn: fetchData,
    config: {
      refetchOnWindowFocus: false,
      staleTime: 50000,
    },
  });
  useEffect(() => {
    if (threadStatusesStatus === "success" && threadStatuses.length > 0) {
      setFilter(threadStatuses[0]);
    }
  }, [threadStatuses, threadStatusesStatus]);

  async function getMoreThreads(queryKey, page = 0) {
    let data = await fetchData(
      `channels/${channelID}/threads?page=${page}&pageSize=${PAGE_SIZE}&status=${filter}`
    );
    setTotalThreadsCount(data.totalCount);
    return { data: data.data, page };
  }
  const {
    data: fetchedThreads,
    status: threadsStatus,
    refetch: threadRefetch,
    fetchMore: fetchMoreThreads,
    canFetchMore: canFetchMoreThreads,
    isFetchingMore: isFetchingMoreThreads,
  } = useInfiniteQuery({
    queryKey: `${
      !showSearchInput
        ? `channels/${channelID}/threads?page=0&pageSize=10&status=${filter}`
        : showSearchInput && searchByQuery
        ? `channels/${channelID}/threads/search?searchQuery=${searchQuery}`
        : null
    }`,
    queryFn: !showSearchInput
      ? getMoreThreads
      : showSearchInput && searchByQuery
      ? fetchData
      : null,
    config: {
      getFetchMore: (lastGroup, allGroups) =>
        lastGroup.page * PAGE_SIZE >= totalThreadsCount
          ? false
          : lastGroup.page + 1,
      refetchOnWindowFocus: false,
      enabled: channelID ? true : false,
    },
  });
  const handleToggleSearchInput = () => {
    setShowSearchInput(!showSearchInput);
  };

  const [selectedThread, setSelectedThread] = useState();
  useEffect(() => {
    if (
      threadsStatus === "success" &&
      fetchedThreads &&
      fetchedThreads[0].data &&
      fetchedThreads[0].data.length > 0
    ) {
      setSelectedThread(fetchedThreads[0].data[0]);
    } else {
      setSelectedThread();
    }
  }, [fetchedThreads, threadsStatus]);

  async function changeThreadStatus(status) {
    setIsChangingThreadStatus(true);
    try {
      await sendData(`threads/${selectedThread.id}/status?status=${status}`);
      threadRefetch();
    } catch (error) {
      console.log(error);
    }
    setIsChangingThreadStatus(false);
  }

  return (
    <>
      <main className={classes.content}>
        <div className={classes.root}>
          <ResizableBox
            className="box"
            height={windowHeight}
            width={300}
            axis="x"
            resizeHandles={["e"]}
          >
            <div>
              <AppBar position="sticky">
                <Toolbar className={classes.toolbar}>
                  <Zoom in={!showSearchInput}>
                    <Paper
                      className={clsx({
                        [classes.tools]: !showSearchInput,
                        [classes.hide]: showSearchInput,
                      })}
                    >
                      <FilterMenuForThreads
                        onMenuChange={(selectedType) => {
                          setFilter(selectedType);
                        }}
                        threadStatuses={threadStatuses}
                        threadStatusesStatus={threadStatusesStatus}
                      />
                      <IconButton
                        aria-label="delete"
                        onClick={handleToggleSearchInput}
                        size="small"
                      >
                        <SearchIcon />
                      </IconButton>
                    </Paper>
                  </Zoom>
                  <Zoom in={showSearchInput}>
                    <Paper
                      className={clsx({
                        [classes.tools]: showSearchInput,
                        [classes.hide]: !showSearchInput,
                      })}
                    >
                      <SearchBox
                        value={searchQuery}
                        onToggle={handleToggleSearchInput}
                        show={showSearchInput}
                        onChange={(e) => {
                          setSearchByQuery(false);
                          setSearchQuery(e.target.value);
                        }}
                        onSearchQueryClick={() => setSearchByQuery(true)}
                      />
                    </Paper>
                  </Zoom>
                </Toolbar>
              </AppBar>

              <Paper variant={"outlined"}>
                <List className={classes.list}>
                  {threadsStatus === "loading" ? (
                    [...Array(20)].map((e, i) => (
                      <React.Fragment key={i}>
                        <ListItem alignItems="flex-start" key={i}>
                          <Box width={"100%"}>
                            <Skeleton />
                            <Skeleton width="60%" />
                          </Box>
                        </ListItem>
                        <Divider variant="middle" component="li" />
                      </React.Fragment>
                    ))
                  ) : threadsStatus === "success" &&
                    fetchedThreads &&
                    fetchedThreads.data &&
                    fetchedThreads.data.length < 1 ? (
                    <h2 style={{ textAlign: "center", color: "#5b5b5b" }}>
                      No Thread
                    </h2>
                  ) : threadsStatus === "success" &&
                    fetchedThreads.length > 0 ? (
                    fetchedThreads.map((threadsGroup, i) =>
                      threadsGroup.data.map((thread) => {
                        return (
                          <React.Fragment key={thread.id}>
                            <ListItem
                              button
                              alignItems="flex-start"
                              selected={selectedThread?.id === thread.id}
                              onClick={() => setSelectedThread(thread)}
                            >
                              <ListItemAvatar>
                                <FirstCharOfName
                                  size="small"
                                  firstName={
                                    thread.contacts[0].contactFirstName[0]
                                  }
                                  lastName={
                                    thread.contacts[0].contactLastName[0]
                                  }
                                />
                              </ListItemAvatar>
                              <ListItemText
                                primary={thread.subject}
                                secondary={
                                  <React.Fragment>
                                    <Typography
                                      component="span"
                                      variant="body2"
                                      className={classes.inline}
                                      color="textPrimary"
                                    >
                                      {thread.tag}
                                      {" - "}
                                    </Typography>
                                    <Typography variant="caption">
                                      {showDate(thread.createdDate)}
                                    </Typography>
                                  </React.Fragment>
                                }
                              />
                            </ListItem>
                            <Divider component="li" />
                          </React.Fragment>
                        );
                      })
                    )
                  ) : threadsStatus === "error" ? (
                    <RefresherComponent handleRefresh={threadRefetch} />
                  ) : null}
                  {!isFetchingMoreThreads &&
                  canFetchMoreThreads * PAGE_SIZE < totalThreadsCount ? (
                    <Waypoint onEnter={() => fetchMoreThreads()} />
                  ) : isFetchingMoreThreads ? (
                    <div
                      style={{
                        margin: "10px 0px",
                        display: "flex",
                        justifyContent: "center",
                      }}
                    >
                      <CircularProgress />
                    </div>
                  ) : null}
                </List>
              </Paper>
            </div>
          </ResizableBox>
          <div style={{ flex: "1" }}>
            <MessageInfo
              threadRefetch={threadRefetch}
              selectedThread={selectedThread}
              onChangeStatus={changeThreadStatus}
              isChangingThreadStatus={isChangingThreadStatus}
            />
          </div>
        </div>
      </main>
      <Customer Contacts={selectedThread?.contacts} />
    </>
  );
};

export default Threads;
