import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Button from "@material-ui/core/Button";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import { showDate } from "./helper";

const useStyles = makeStyles((theme) => ({
  card: {
    maxWidth: "70%",
    marginLeft: "auto",
    marginRight: "auto",
    marginTop: "10px",
    overflowY: "hidden",
    border: "2px solid #ffeb00",
    borderRadius: "9px 9px 9px 9px",
    backgroundColor: "#fef8f0",
  },
  content: {
    display: "flex",
    alignItems: "center",
    wordBreak: "break-word",
  },
  commentText: {
    padding: 2,
    textAlign: "center",
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

export default function ShowCommentReply(props) {
  const classes = useStyles();
  let content = document.getElementById(`content-${props.message.id}`);
  const [isOverflow, setIsOverflow] = useState(
    content && content.scrollHeight > content.clientHeight ? true : false
  );

  useEffect(() => {
    setIsOverflow(
      content && content.scrollHeight > content.clientHeight ? true : false
    );
  }, [content]);

  const [showMore, setShowMore] = useState(false);

  return (
    <Card className={classes.card}>
      <CardHeader
        avatar={
          <Avatar className={classes.avatar}>
            {props.message.createdBy
              ? `${props.message.createdBy.firstName.charAt(
                  0
                )}${props.message.createdBy.lastName.charAt(0)}`
              : "?"}
          </Avatar>
        }
        title={
          <Typography variant="h6" color="secondary">
            {props.message.createdBy
              ? `${props.message.createdBy.firstName} ${props.message.createdBy.lastName}`
              : "?"}
          </Typography>
        }
        subheader={
          <Typography color="primary">
            {showDate(props.message.createdDate)}
          </Typography>
        }
      />
      <CardContent className={classes.content}>
        <div id={`container-${props.message.id}`}>
          <div
            id={`content-${props.message.id}`}
            dangerouslySetInnerHTML={{ __html: props.message.body }}
            style={{
              whiteSpace: "normal",
              ...(!showMore && !props.isOnlyOne
                ? { maxHeight: "370px" }
                : null),
              ...(!showMore && !props.isOnlyOne
                ? { overflow: "hidden" }
                : null),
              wordWrap: "break-word",
            }}
          />
          {isOverflow && (
            <div
              style={{
                marginTop: "10px",
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <Button onClick={() => setShowMore((i) => !i)} variant="outlined">
                {showMore ? "show less" : "show more"}
              </Button>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
