import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import SendIcon from "@material-ui/icons/Send";
import htmlToDraft from "html-to-draftjs";
import draftToHtml from "draftjs-to-html";
import { convertToRaw, EditorState } from "draft-js";

import { Editor } from "react-draft-wysiwyg";
import "../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import { sendData } from "../services/httpUtil";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Box
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={1}>{children}</Box>}
    </Box>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "0px",
    height: "100%",
    display: "flex",
    flexFlow: "column",
  },
  rootText: {
    "& > *": {
      margin: theme.spacing(1),
    },
    textInput: {
      overflowY: "hidden",
    },
  },
  editor: {
    padding: "0px 5px",
    maxHeight: "30vh",
    overflow: "auto",
    maxWidth: "100%",
    whiteSpace: "prewrap",
    overflowWrap: "break-word",
    wordBreak: "break-word",
  },
  tab: {
    // flex: "1",
    // overflow: "auto",
    minHeight: "160px",
    maxWidth: "100%",
  },
  replyTab: {
    backgroundColor: "#fafafa",
  },
  commentTab: {
    backgroundColor: "#fef8f0",
  },
  appBarSection: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
  },
  sendButton: {
    marginLeft: "auto",
    minWidth: "100px",
    marginRight: "10px",
  },
}));

export default function InputText(props) {
  const classes = useStyles();
  const [indexValue, setIndexValue] = useState(
    props.threadStatus === "CLOSED" || props.threadStatus === "COMPLETED"
      ? 1
      : 0
  );
  const [replyState, setReplyState] = useState(EditorState.createEmpty());
  const [commentState, setCommentState] = useState(EditorState.createEmpty());
  const handleChange = (event, newValue) => {
    setIndexValue(newValue);
  };
  return (
    <div className={classes.root}>
      <AppBar position="static" className={classes.appBarSection}>
        <Tabs
          value={indexValue}
          onChange={handleChange}
          aria-label="simple tabs example"
        >
          <Tab
            label="Reply"
            {...a11yProps(0)}
            disabled={
              props.threadStatus === "CLOSED" ||
              props.threadStatus === "COMPLETED"
            }
          />
          <Tab label="Comment" {...a11yProps(1)} />
        </Tabs>
        <Button
          variant="contained"
          endIcon={<SendIcon />}
          className={classes.sendButton}
          disabled={
            (indexValue === 0 &&
              replyState &&
              replyState.getCurrentContent() &&
              convertToRaw(replyState.getCurrentContent()).blocks[0].text <
                1) ||
            (indexValue === 1 &&
              commentState &&
              commentState.getCurrentContent() &&
              convertToRaw(commentState.getCurrentContent()).blocks[0].text < 1)
          }
          onClick={async () => {
            let body = {
              body:
                indexValue === 0
                  ? draftToHtml(convertToRaw(replyState.getCurrentContent()))
                  : draftToHtml(convertToRaw(commentState.getCurrentContent())),
              type: indexValue === 0 ? "EMAIL" : "COMMENT",
              visibility: "PUBLIC",
            };
            try {
              props.onIsSendingMessage(true);
              await sendData(`threads/${props.threadId}/messages`, body);
              props.messagesRefresh();
            } catch (error) {
              console.log(error);
            }
            setReplyState(EditorState.createEmpty());
            setCommentState(EditorState.createEmpty());
            props.onIsSendingMessage(false);
          }}
        >
          Send
        </Button>
      </AppBar>
      <TabPanel
        id="reply-input-section"
        value={indexValue}
        index={0}
        className={clsx(classes.tab, classes.replyTab)}
      >
        <Editor
          editorState={replyState}
          onEditorStateChange={(text) => {
            setReplyState(text);
          }}
          editorClassName={classes.editor}
          toolbar={{
            options: ["inline", "link", "list"],
            inline: {
              inDropdown: false,
              className: undefined,
              component: undefined,
              dropdownClassName: undefined,
              options: ["bold", "italic", "underline"],
            },
          }}
        />
      </TabPanel>
      <TabPanel
        id="comment-input-section"
        value={indexValue}
        index={1}
        className={clsx(classes.tab, classes.commentTab)}
      >
        <Editor
          editorState={commentState}
          onEditorStateChange={(text) => {
            setCommentState(text);
          }}
          editorClassName={classes.editor}
          toolbar={{
            options: ["inline", "link", "list"],
            inline: {
              inDropdown: false,
              className: undefined,
              component: undefined,
              dropdownClassName: undefined,
              options: ["bold", "italic", "underline"],
            },
          }}
        />
      </TabPanel>
    </div>
  );
}
