import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import { fade } from "@material-ui/core/styles/colorManipulator";

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    "& > *": {
      margin: theme.spacing(1)
    }
  },
  orange: {
    color: theme.palette.primary.dark,
    backgroundColor: fade(theme.palette.primary.light, 0.5)
  },
  small: {
    fontSize: theme.spacing(2),
    width: theme.spacing(4),
    height: theme.spacing(4)
  },
  medium: {
    fontSize: theme.spacing(5),
    width: theme.spacing(7),
    height: theme.spacing(7)
  },
  large: {
    width: theme.spacing(14),
    height: theme.spacing(14),
    fontSize: theme.spacing(8)
  }
}));

const FirstCharOfName = props => {
  const classes = useStyles();
  let size;
  switch (props.size) {
    case "small":
      size = classes.small;
      break;
    case "medium":
      size = classes.medium;
      break;
    case "large":
    default:
      size = classes.large;
      break;
  }
  var initials =
    props.firstName[0].toUpperCase() + "" + props.lastName[0].toUpperCase();
  return (
    <div className={classes.root}>
      <Avatar className={`${classes.orange} ${size}`}>{initials}</Avatar>
    </div>
  );
};

export default FirstCharOfName;
