import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";

import CssBaseline from "@material-ui/core/CssBaseline";
import Channels from "./layouts/channels";
import Threads from "./layouts/threads";
import { fetchData } from "./services/httpUtil";
import { useQuery } from "react-query";
import { ReactQueryDevtools } from "react-query-devtools";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const drawerWidth = "15%";

const useStyles = makeStyles((theme) => ({
  smallScreenSection: {
    backgroundColor: "#f1f1f1",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    marginTop: "20%",
    textAlign: "center",
  },
  root: {
    display: "flex",
  },
  menuButton: {
    width: 42,
    height: 42,
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(0),
    height: "100vh",
  },
  backdropRoot: {
    display: "flex",
    "& > * + *": {
      marginLeft: theme.spacing(1),
      alignItems: "center",
      justify: "center",
      height: "100vh",
    },
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const App = () => {
  const classes = useStyles();
  const isSmallScreen = useMediaQuery("(min-width:700px)");
  const [selectedChannelID, setSelectedChannelID] = useState();

  const {
    data: fetchedChannels,
    status: channelsStatus,
    refetch: channelsRefetch,
  } = useQuery({
    queryKey: "channels",
    queryFn: fetchData,
    config: { refetchOnWindowFocus: false },
  });

  useEffect(() => {
    if (channelsStatus === "success" && fetchedChannels.length > 0) {
      setSelectedChannelID(fetchedChannels[0].id);
    }
  }, [channelsStatus, fetchedChannels]);

  useEffect(() => {
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/x-www-form-urlencoded");

    let data = new FormData();
    data.append("username", "ashkala");
    data.append("password", "block-monitored-policebox");

    let requestOptions = {
      method: "POST",
      // headers: myHeaders,
      body: data,
      redirect: "follow",
    };
    fetch("https://app.rerumapp.uk/test/authenticate", requestOptions)
      .then((response) => response.text())
      .then((result) => localStorage.setItem("TOKEN", JSON.parse(result).token))
      .catch((error) => console.log("error in token", error));
  }, []);
  if (!isSmallScreen) {
    return (
      <div className={classes.smallScreenSection}>
        <h3>The page cannot be displayed on this screen</h3>
      </div>
    );
  }
  return (
    <div className={classes.root}>
      {process.env.REACT_APP_ENV === "DEV" ? <ReactQueryDevtools /> : null}
      <CssBaseline />
      <Channels
        status={channelsStatus}
        refetch={channelsRefetch}
        channels={fetchedChannels}
        onSelectedChannelChange={(channelID) => {
          setSelectedChannelID(channelID);
        }}
        selectedChannelId={selectedChannelID}
      />

      <Threads channelID={selectedChannelID} />
    </div>
  );
};

export default App;
