import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MailIcon from "@material-ui/icons/Mail";
import CircularProgress from "@material-ui/core/CircularProgress";
import Badge from "@material-ui/core/Badge";
import RefreshIcon from "@material-ui/icons/Refresh";
import { Typography } from "@material-ui/core";

const drawerWidth = "15%";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBarHeight: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    backgroundColor: theme.palette.primary.main,
    ...theme.mixins.toolbar,
  },
  menuButton: {
    width: 42,
    height: 42,
  },
  menuButtonHide: {
    width: 42,
    height: 42,
  },
  hide: {
    display: "none",
  },
  drawer: {
    display: "flex",
    flexFlow: "column",
    height: "100vh",
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    overflowY: "auto",

    // backgroundColor: "#cecece",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    transitionTimingFunction: "cubic-bezier(0.6, -0.28, 0.74, 0.05)",
    transitionDuration: "500ms",
  },
  drawerClose: {
    // backgroundColor: "#ffffff",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(5) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(5) + 1,
    },
  },
  list: {
    paddingLeft: 8,
    minWidth: 5,
  },
  textInList: {
    fontSize: 8,
    padding: -10,
  },
  iconInList: {
    minWidth: 35,
  },
  link: {
    marginTop: 80,
  },
  channelsSection: {
    flex: "1",
    overflowY: "auto",
    overflowX: "hidden",
  },
}));

const Channels = (props) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleChannelClick = (event, channelID) => {
    props.onSelectedChannelChange(channelID);
  };

  return (
    <Drawer
      variant="permanent"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: open,
        [classes.drawerClose]: !open,
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        }),
      }}
    >
      <div className={classes.appBarHeight}>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={props.status === "error" ? props.refetch : handleDrawerOpen}
          className={clsx(classes.menuButton, {
            [classes.hide]: open,
          })}
        >
          {props.status === "error" ? <RefreshIcon /> : <MenuIcon />}
        </IconButton>
        <div
          className={clsx({
            [classes.hide]: !open,
          })}
          style={{ margin: "auto" }}
        >
          <Typography variant="h5" align="center">
            Channel
          </Typography>
        </div>

        <IconButton
          className={clsx(classes.menuButtonHide, {
            [classes.hide]: !open,
          })}
          onClick={handleDrawerClose}
        >
          <ChevronLeftIcon />
        </IconButton>
      </div>

      <div className={classes.channelsSection}>
        <Divider />
        {props.status === "error" ? (
          <div>Failed to load channels</div>
        ) : props.status === "loading" ? (
          <CircularProgress />
        ) : props.status === "success" && props.channels ? (
          <List>
            {props.channels.map((ch, index) => (
              <ListItem
                button
                className={classes.list}
                key={ch.id}
                selected={props.selectedChannelId === ch.id}
                onClick={(event) => handleChannelClick(event, ch.id)}
              >
                <ListItemIcon className={classes.iconInList}>
                  <Badge color="secondary" max={99} badgeContent={ch.count}>
                    <MailIcon />
                  </Badge>
                </ListItemIcon>
                <ListItemText
                  primary={ch.name}
                  className={classes.textInList}
                />
              </ListItem>
            ))}
          </List>
        ) : (
          <div>Error</div>
        )}
      </div>
    </Drawer>
  );
};

export default Channels;
