import React, { useEffect } from "react";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import { CircularProgress } from "@material-ui/core";

export default function FilterMenuForThreads(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [text, setText] = React.useState("");

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const { threadStatuses, threadStatusesStatus } = props;

  useEffect(() => {
    if (threadStatusesStatus === "success" && threadStatuses.length > 0) {
      setText(threadStatuses[0]);
    }
  }, [threadStatuses, threadStatusesStatus]);

  const handleClose = (event) => {
    if (event.currentTarget.textContent) {
      setText(event.currentTarget.textContent);
    } else if (
      threadStatusesStatus === "success" &&
      threadStatuses.length > 0
    ) {
      setText(threadStatuses[0]);
    }
    setAnchorEl(null);
  };

  return (
    <>
      {threadStatusesStatus === "success" ? (
        <div>
          <Button
            aria-controls="simple-menu"
            aria-haspopup="true"
            onClick={handleClick}
            endIcon={<ArrowDropDownIcon />}
          >
            {text}
          </Button>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            {threadStatuses &&
              threadStatuses.map((key) => {
                return (
                  <MenuItem
                    key={key}
                    onClick={(event) => {
                      handleClose(event);
                      props.onMenuChange(`${key}`);
                    }}
                  >
                    {key}
                  </MenuItem>
                );
              })}
          </Menu>
        </div>
      ) : (
        <CircularProgress color="secondary" />
      )}
    </>
  );
}
