import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import { showDate } from "./helper";
import { Button } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  card: {
    width: "93%",
    marginLeft: "5%",
    marginTop: "10px",
    // maxHeight: "30%",
    overflowY: "auto",
    backgroundColor: "#fafafa",
  },
  avatar: {
    backgroundColor: theme.palette.primary.main,
  },
  header: {
    backgroundColor: theme.palette.primary,
    color: "white",
  },
}));

export default function MessageContent(props) {
  const classes = useStyles();
  let content = document.getElementById(`content-${props.message.id}`);
  const [isOverflow, setIsOverflow] = useState(
    content && content.scrollHeight > content.clientHeight ? true : false
  );

  useEffect(() => {
    setIsOverflow(
      content && content.scrollHeight > content.clientHeight ? true : false
    );
  }, [content]);

  const [showMore, setShowMore] = useState(false);
  return (
    <Card className={classes.card}>
      <CardHeader
        avatar={
          <Avatar className={classes.avatar}>
            {props.message.createdBy
              ? `${props.message.createdBy.firstName.charAt(
                  0
                )}${props.message.createdBy.lastName.charAt(0)}`
              : "?"}
          </Avatar>
        }
        title={
          <Typography variant="h6" color="primary">
            {props.message.createdBy
              ? `${props.message.createdBy.firstName} ${props.message.createdBy.lastName}`
              : "?"}
          </Typography>
        }
        subheader={
          <Typography color="secondary">
            {showDate(props.message.createdDate)}
          </Typography>
        }
        className={classes.header}
      />
      <CardContent style={{ wordBreak: "break-word" }}>
        <div id={`container-${props.message.id}`}>
          <div
            id={`content-${props.message.id}`}
            dangerouslySetInnerHTML={{ __html: props.message.body }}
            style={{
              whiteSpace: "normal",
              ...(!showMore && !props.isOnlyOne
                ? { maxHeight: "370px" }
                : null),
              ...(!showMore && !props.isOnlyOne
                ? { overflow: "hidden" }
                : null),
            }}
          />
          {isOverflow && (
            <div
              style={{
                marginTop: "10px",
                width: "100%",
                display: "flex",
                justifyContent: "center",
              }}
            >
              <Button onClick={() => setShowMore((i) => !i)} variant="outlined">
                {showMore ? "show less" : "show more"}
              </Button>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
