import React, { useEffect } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import PersonIcon from "@material-ui/icons/Person";
import Fade from "@material-ui/core/Fade";
import ContactProfile from "../components/contactProfile";
import ContactDetails from "../components/contactDetails";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import { Typography } from "@material-ui/core";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const drawerWidth = "20%";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appbarHieght: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start",
    backgroundColor: theme.palette.primary.main,
    ...theme.mixins.toolbar,
  },
  menuButton: {
    width: 42,
    height: 42,
  },
  hide: {
    display: "none",
  },
  drawer: {
    height: "100vh",
    display: "flex",
    flexFlow: "column",
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    [theme.breakpoints.down("md")]: {
      width: "50vh",
    },
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    transitionTimingFunction: "cubic-bezier(0.6, -0.28, 0.74, 0.05)",
    transitionDuration: "500ms",
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(4) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(5) + 1,
    },
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(0),
  },
  customerInfoSection: {
    paddingTop: "5px",
    flex: "1",
    overflow: "auto",
    display: "flex",
    flexFlow: "column",
  },
}));

const Customer = ({ Contacts }) => {
  const classes = useStyles();
  const [openDrawer, setOpenDrawer] = React.useState(false);
  const [openSnackbar, setOpenSnackbar] = React.useState(false);

  const selectedContact = Contacts && Contacts[0];
  const handleCloseSnackbar = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenSnackbar(false);
  };
  useEffect(() => {
    if (!selectedContact) {
      setOpenDrawer(false);
    } else {
      setOpenDrawer(true);
    }
  }, [selectedContact]);

  const handleDrawerOpen = () => {
    !selectedContact ? setOpenSnackbar(true) : setOpenDrawer(true);
  };
  const handleDrawerClose = () => {
    setOpenDrawer(false);
  };
  return (
    <Drawer
      variant="permanent"
      anchor="right"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: openDrawer,
        [classes.drawerClose]: !openDrawer,
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: openDrawer,
          [classes.drawerClose]: !openDrawer,
        }),
      }}
    >
      <div className={classes.appbarHieght}>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={handleDrawerOpen}
          className={clsx(classes.menuButton, {
            [classes.hide]: openDrawer,
          })}
          disabled={!selectedContact || selectedContact.length < 1}
        >
          <PersonIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.menuButton, {
            [classes.hide]: !openDrawer,
          })}
          onClick={handleDrawerClose}
        >
          <ChevronRightIcon />
        </IconButton>
        <div
          className={clsx({
            [classes.hide]: !openDrawer,
          })}
          style={{ margin: "auto" }}
        >
          <Typography
            variant="h5"
            align="center"
            style={{ marginRight: "48px" }}
          >
            Contact
          </Typography>
        </div>
      </div>
      <Fade
        in={openDrawer}
        timeout={{ enter: 1000, exit: 500 }}
        className={clsx({
          [classes.hide]: !openDrawer,
        })}
      >
        <div
          style={{
            flex: "1",
            overflow: "auto",
            display: "flex",
            flexFlow: "column",
          }}
        >
          <div
            className={clsx(classes.customerInfoSection, {
              [classes.hide]: !openDrawer,
            })}
          >
            <ContactProfile
              selectedContact={selectedContact ? selectedContact : "Unknown"}
            />
            <Divider />
            <ContactDetails
              selectedContact={selectedContact ? selectedContact : "Unknown"}
            />
          </div>
        </div>
      </Fade>
      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        open={openSnackbar}
        autoHideDuration={4000}
        onClose={handleCloseSnackbar}
      >
        <Alert severity="error">Contact not selected</Alert>
      </Snackbar>
    </Drawer>
  );
};

export default Customer;
